  --main-color: #006989;
  --secundary-color: #E88D67;
  --complementary-color: #F3F7EC;
  --header-height: 130px;
}

*, *::after, *::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: auto;
  text-decoration: none;
  list-style: none;
}
html{
  background-color: var(--complementary-color);
}
body{
  display: flex;
  flex-direction: column;
  min-height: 100svh;  
}
#content{
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - var(--header-height));
}
.container{
  display: flex;
  flex-direction: column;
  height: calc(100vh - var(--header-height));
}
.container-sm{
  width: inherit;
  margin: 0 20%;  
}
.container-lg{
  width: inherit;
  flex-grow: 1;
