___CSS_LOADER_EXPORT___.push([module.id, `.hero{  
  display: grid;
  grid-template-columns: repeat(2, 1fr);  
  padding-top: 50px;  
}
.hero__title{
  font-family: 'Montaga';
  font-size: 5rem;
  font-weight: normal;
  color: var(--main-color);
}
.hero__text{
  margin-top: 20px;
  font-family: 'Montagu slab';
  color: var(--secundary-color);
}
.hero__text>p{  
  font-size: 1rem;
  font-weight: 400;
}
.hero__text>h2{
  font-size: 2rem;
  font-weight: 500;
}
.call-to-action{
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: end;
}
.call-to-action>button{
  width: 50%;
  padding: 16px 0;
  border: none;
  border-radius: 10px;
  background-color: var(--main-color);
  color: var(--complementary-color);
  font-family: 'Montagu slab';
  font-size: 1.5rem;
  cursor: pointer;
  transition: all .2s ease-in-out;
}
.call-to-action>button:hover{
  transform: translateY(-2px);
  -webkit-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.27);
  -moz-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.27);
  box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.27);
}
.call-to-action>h2{
  width: 50%;
  margin-bottom: 5px;
  text-align: center;
  font-family: 'Montagu slab';  
  font-weight: 400;
  color: var(--main-color);
}

.hero-img{
  flex: 1;
  margin-top: 25px;
  border-top: 2px solid var(--secundary-color);
  overflow: hidden;  
}
.hero-img>img{
  width: 100%;
}`, "",{"version":3,"sources":["webpack://./src/css/components/home.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,qCAAqC;EACrC,iBAAiB;AACnB;AACA;EACE,sBAAsB;EACtB,eAAe;EACf,mBAAmB;EACnB,wBAAwB;AAC1B;AACA;EACE,gBAAgB;EAChB,2BAA2B;EAC3B,6BAA6B;AAC/B;AACA;EACE,eAAe;EACf,gBAAgB;AAClB;AACA;EACE,eAAe;EACf,gBAAgB;AAClB;AACA;EACE,aAAa;EACb,sBAAsB;EACtB,oBAAoB;EACpB,gBAAgB;AAClB;AACA;EACE,UAAU;EACV,eAAe;EACf,YAAY;EACZ,mBAAmB;EACnB,mCAAmC;EACnC,iCAAiC;EACjC,2BAA2B;EAC3B,iBAAiB;EACjB,eAAe;EACf,+BAA+B;AACjC;AACA;EACE,2BAA2B;EAC3B,qDAAqD;EACrD,kDAAkD;EAClD,6CAA6C;AAC/C;AACA;EACE,UAAU;EACV,kBAAkB;EAClB,kBAAkB;EAClB,2BAA2B;EAC3B,gBAAgB;EAChB,wBAAwB;AAC1B;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,4CAA4C;EAC5C,gBAAgB;AAClB;AACA;EACE,WAAW;AACb","sourcesContent":[".hero{  \n  display: grid;\n  grid-template-columns: repeat(2, 1fr);  \n  padding-top: 50px;  \n}\n.hero__title{\n  font-family: 'Montaga';\n  font-size: 5rem;\n  font-weight: normal;\n  color: var(--main-color);\n}\n.hero__text{\n  margin-top: 20px;\n  font-family: 'Montagu slab';\n  color: var(--secundary-color);\n}\n.hero__text>p{  \n  font-size: 1rem;\n  font-weight: 400;\n}\n.hero__text>h2{\n  font-size: 2rem;\n  font-weight: 500;\n}\n.call-to-action{\n  display: flex;\n  flex-direction: column;\n  justify-content: end;\n  align-items: end;\n}\n.call-to-action>button{\n  width: 50%;\n  padding: 16px 0;\n  border: none;\n  border-radius: 10px;\n  background-color: var(--main-color);\n  color: var(--complementary-color);\n  font-family: 'Montagu slab';\n  font-size: 1.5rem;\n  cursor: pointer;\n  transition: all .2s ease-in-out;\n}\n.call-to-action>button:hover{\n  transform: translateY(-2px);\n  -webkit-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.27);\n  -moz-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.27);\n  box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.27);\n}\n.call-to-action>h2{\n  width: 50%;\n  margin-bottom: 5px;\n  text-align: center;\n  font-family: 'Montagu slab';  \n  font-weight: 400;\n  color: var(--main-color);\n}\n\n.