___CSS_LOADER_EXPORT___.push([module.id, `.about{
  display: flex;
  flex-direction: column;
  padding-top: 50px;
}
.about-title{
  position: relative;
  flex: 1;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-bottom: 8px solid var(--secundary-color);
}
.about-title>h1,
.about-title>p{  
  position: absolute;
  font-family: 'Montagu slab';
  color: var(--complementary-color);
}
.about-title>h1{
  top: 125px;
  left: 375px;
  font-size: 3rem;
  font-weight: 500;
}
.about-title>p{
  top: 210px;
  left: 495px;
  width: 400px;
  font-size: .75rem;
  font-weight: 400;
}

.main{
  display: grid;  
  grid-template-columns: repeat(2, 1fr);  
  flex: 1;
  padding: 40px 0;  
}
.main__text{
  display: flex;
  flex-direction: column;  
  border-left: 5px solid black;
  padding-left: 20px;
  padding-right: 20px;  
}
.main__text>p{
  margin-bottom: 16px;
  font-family: 'Montagu slab';
  font-size: .75rem;
  font-weight: 300;  
}
.main__img{
  display: flex;
  align-items: center;
  padding-left: 100px;
}
.main__img>img{
  width: 225px;
  height: 225px;
}`, "",{"version":3,"sources":["webpack://./src/css/components/about.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,iBAAiB;AACnB;AACA;EACE,kBAAkB;EAClB,OAAO;EACP,4BAA4B;EAC5B,sBAAsB;EACtB,2BAA2B;EAC3B,+CAA+C;AACjD;AACA;;EAEE,kBAAkB;EAClB,2BAA2B;EAC3B,iCAAiC;AACnC;AACA;EACE,UAAU;EACV,WAAW;EACX,eAAe;EACf,gBAAgB;AAClB;AACA;EACE,UAAU;EACV,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,qCAAqC;EACrC,OAAO;EACP,eAAe;AACjB;AACA;EACE,aAAa;EACb,sBAAsB;EACtB,4BAA4B;EAC5B,kBAAkB;EAClB,mBAAmB;AACrB;AACA;EACE,mBAAmB;EACnB,2BAA2B;EAC3B,iBAAiB;EACjB,gBAAgB;AAClB;AACA;EACE,aAAa;EACb,mBAAmB;EACnB,mBAAmB;AACrB;AACA;EACE,YAAY;EACZ,aAAa;AACf","sourcesContent":[".about{\n  display: flex;\n  flex-direction: column;\n  padding-top: 50px;\n}\n.about-title{\n  position: relative;\n  flex: 1;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: center;\n  border-bottom: 8px solid var(--secundary-color);\n}\n.about-title>h1,\n.about-title>p{  \n  position: absolute;\n  font-family: 'Montagu slab';\n  color: var(--complementary-color);\n}\n.about-title>h1{\n  top: 125px;\n  left: 375px;\n  font-size: 3rem;\n  font-weight: 500;\n}\n.about-title>p{\n  top: 210px;\n  left: 495px;\n  width: 400px;\n  font-size: .75rem;\n  font-weight: 400;\n}\n\n.main{\n  display: grid;  \n  grid-template-columns: repeat(2, 1fr);  \n  flex: 1;\n  padding: 40px 0;  \n}\n.main__text{\n  display: flex;\n  flex-direction: column;  \n  border-left: 5px solid black;\n  padding-left: 20px;\n  padding-right: 20px;  \n}\n.main__text>p{\n  margin-bottom: 16px;\n  font-family: 'Montagu slab';\n  font-size: .75rem;\n  font-weight: 300;  \n}\n.main__img{\n  display: flex;\n  align-items: center;\n  padding-left: 100px;\n}\n.main__img>img{\n  width: 225px;\n  height: 225px;\n}"],"sourceRoot":""}]);
